.section{
	@media(min-width: 768px){
		padding:60px 0;
	}
	padding:30px 0;
}

////////////////// bg
.white_bg {
    background: #fff;
}
.main_color_bg {
    background: $firstMainColor;
}
.second_color_bg {
    background: $MainBGColor;
}
.light_gray_bg {
    background: $lightGray;
}


///////// color
.first_color {
    color: $firstMainColor;
}
.second_color {
    color: $secondMainColor;
}

.placeholder_color {
    color: $PlaceholderBGColor
}

//////////////////// text
.Open_Sans_font {
    font-family: 'Open Sans', sans-serif;
}

.fs-7 {
    font-size: 0.9rem;
}

.w-fit {
    width: fit-content;
}

// Buttons

._borders {
    border: 2px solid $firstMainColor;
}

.mega-about {
    display: none !important;
    @include trans(0.4s);
}

.btn_anim {
    min-width: 9rem;
    width: fit-content;
    text-align: center;
    @include trans(0.4s);

    &:hover {
        background: $firstMainColor;
        color: $lightGray;
        @include trans(0.4s);
    }

}

.mega {
    @include trans(0.4s);

    &:hover  .mega-about {
        display: flex !important;
        @include trans(0.4s);
    }
}

.mega_res {
    min-width: 9rem;

    .w_mega {
        min-width: 9rem;
    }

    .btn_anim_res {
        background: $firstMainColor;
        color: $lightGray;
        min-width: 9rem;
    }

    .hover_btn {
        @include trans(0.4s);

        &:hover {
            background: $firstMainColor;
            color: $lightGray;
        }
    }
}

.margin-negative {
    margin-top: -1rem;
}

.z-index-back {
    z-index: -1;
}

.w-content-75 {
    width: 100%;
    @media (min-width: 992px) {
        width: 75%;
    }
}

.w-content-50 {
    width: 100%;
    @media (min-width: 992px) {
        width: 50%;
    }
}


.position-img {
    position: relative;
    @media (min-width: 992px) {
        bottom: 30%;
    }
}
.position-img-reach {
    position: relative;
    @media (min-width: 992px) {
        bottom: 10%;
    }
}
.position-img-sec {
    @media (min-width: 992px) {
        top: -28%;
        position: relative; 
    }
}
.position-diploma {
    @media (min-width: 992px) {
        bottom: 44%;
        right: 5%;
    }
}
.position-diploma-cas {
    top: 0;
    @media (min-width: 992px) {
        top: 15%;
    }

    @media only screen and (min-width: 768px) and (max-width: 991.98px) {
        top: 21%;
    }
}

.max-h {
    max-height: max-content;
    @media (min-width: 992px) {
        max-height: 30rem;
    }
}


.padding-start {
    padding-left: 10rem;
}

.shape-line {
    height: 2px;
    border-radius: 4rem;
    margin-left: 3rem;
}


.red_line_section {
    height: 3px;
    width: 85%;
    background: $firstMainColor;
    border-radius: 5rem;
}

////////////////////////////////////////////////////////
////////////////// Start Gallery Page ///////////////////
/////////////////////////////////////////////////////////
.gallery {
    position: relative;
    width: $widthCard;
    height: $widthCard;
    overflow: hidden;
    border-radius: 0.5rem;
    transition: all 0.4s ease;
    margin: 1rem;

}

.gallery::before {
    content: "";
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translateX(-50%) translateY(-50%);
    z-index: 100;
    width: 100%;
    height: 100%;
    background: #b11e2436;
    box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
    backdrop-filter: blur(1.3px);
    -webkit-backdrop-filter: blur(1.3px);
}

.gallery_border {
    position: relative;
    transform: translateX(-50%) translateY(-50%);
    top: 50%;
    left: 50%;
    width: $widthBorder!important;
    height: $widthBorder!important;
    border: 2px solid #ffffff;
    margin: 0;
    overflow: hidden;
    border-radius: 0.5rem;
    background: transparent;
    z-index: 200;
}

.link-address {
    transform: rotateZ( 0deg);
    height: 100%;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}


.gallery .gallery_child img {
    position: absolute;
    border-radius: 0.5rem;
    transform: translateX(-50%) rotateZ( 0deg) translateY(-50%) scale(2.5);
    top: 50%;
    left: 50%;
    z-index: 10;
}


.gallery:hover .gallery_child img  {
    transform: translateX(-50%) rotateZ(0deg) translateY(-50%) scale(2.8);
    border-radius: 0.5rem;
    overflow: hidden;
    transition: all 0.4s ease-in-out;
}

.rotate-dev {
    width: fit-content;
    height: fit-content;
    padding: 0;
    margin: 0;
}

@media (min-width: 992px) {
    .rotate-dev {
        transform: rotateZ(45deg);
        margin-top: 10rem;
    }

    .link-address {
        transform: rotateZ(-45deg);
    }

    .gallery .gallery_child img {
        transform: translateX(-50%) rotateZ(-45deg) translateY(-50%) scale(2.5);
    }

    .gallery:hover .gallery_child img  {
        transform: translateX(-50%) rotateZ(-45deg) translateY(-50%) scale(2.8) !important;
    }
}


/////////////////////////////////////////////////////////
/////////////////////--- Video ----//////////////////////
/////////////////////////////////////////////////////////

.js .slider-single > div:nth-child(1n+2) { 
    display: none 
}

.js .slider-single.slick-initialized > div:nth-child(1n+2) { 
    display: block 
}

.slider-nav .slick-slide { 
    cursor: pointer;
    margin: 2rem 1rem 0rem 1rem;
}

@media (max-width: 991.98px) {
    .slider-nav .slick-slide { 
        cursor: pointer;
        margin: 0.5rem 0rem 0 0.5rem;
    }
    
}

.slick-slide.is-active {
	color: #c00;
	background-color: #fff
}

/////////////////////////////////////////////////////////
/////////////////// End Gallery Page ////////////////////
/////////////////////////////////////////////////////////

/////////////////////////////////////////////////////////
////////////////// Start Facility Page //////////////////
/////////////////////////////////////////////////////////
.center {
    width: 100%;

    .slick-list {
        overflow: visible !important;
    }

    .slick-track {
        display: flex;
        align-items: center;
        flex-wrap: nowrap;
        justify-content: center;
        gap: 0rem;
        
        @media (min-width: 992px) {
            gap: 0.2rem;
        }
    }

    .slick-slide {
        float: none;
        display: inline-block;
        vertical-align: middle;
        margin: 0 20px;
        transition: all 0.3s ease;
        height: auto;
        text-align: center;

        &.slick-current,
        &.slick-center {
            display: inherit;
            z-index: 1000;
            transform: scale(1.1);

            p {
                display: block !important;
            }
        }
    }
}


._overlay {

    &::before {
        content: "";
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translateX(-50%) translateY(-50%);
        z-index: 100;
        width: 100%;
        height: 100%;
        background: rgba(177, 30, 36, 0.21);
        box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
        backdrop-filter: blur(1.8px);
        -webkit-backdrop-filter: blur(1.3px);
    }

    ._link-border {
        position: absolute;
        transform: translateX(-50%) translateY(-50%);
        top: 50%;
        left: 50%;
        width: 90% !important;
        height: 85% !important;
        border: 2px solid #ffffff;
        margin: 0;
        overflow: hidden;
        background: transparent;
        z-index: 200;
    }
}

/////////////////////////////////////////////////////////
//////////////////// End Facility Page //////////////////
/////////////////////////////////////////////////////////
